import Vue from 'vue'
import VeeValidate ,{ Validator } from 'vee-validate'
import zh_CN from 'vee-validate/dist/locale/zh_CN'
import VueI18n from 'vue-i18n'

Vue.use(VueI18n)
const i18n = new VueI18n({
  locale: 'zh_CN',
})
const config = {
  errorBagName: 'errors', 
  fieldsBagName: 'fields',
  delay: 100,   
  locale: 'zh_CN', 
  strict: true,  
  enableAutoClasses: true,
  events: 'focus|input|blur|submit', 
  inject: true
};
Vue.use(VeeValidate,config,{
  i18n,
  i18nRootKey: 'validation',
  dictionary: {
    zh_CN
}})
//
const tel = {
  getMessage: function (name) { return " " },
  validate: (value, args) => {
    return value.length == 11 && /^((13|14|15|17|18)[0-9]{1}\d{8})$/.test(value)
  }
}

const password={
  getMessage:function(name){return name+"6-16"},
  validate:(value, args)=>{
    return /^(?![0-9]+$)(?![a-z]+$)(?![A-Z]+$)(?![,\!\.#%'\+\*\-:;^_`]+$)[,\!\.#%'\+\*\-:;^_`0-9A-Za-z]{6,16}$/.test(value)
  },
}
const cnLength={
  getMessage:function(name,args){return name+""+args[0]+""},
  validate:(value, args)=>{
    var valLen=0;
    if(typeof value=="number"){
      valLen=value.length;
    }else{
      valLen=value.replace(/[\u0391-\uFFE5]/g,"aa").length;
    }
    return valLen<=args[0];
  },
}
const idnumber={
  getMessage:function(name,args){return name+""},
  validate:(value, args)=>{
    return /(^\d{110}$)|(^\d{112}$)|(^\d{17}(\d|X|x)$)/.test(value)
  },
}
// name htmldata-vv-asnameargsargs[0],args[1]args[2]
//
VeeValidate.Validator.localize({
  zh_CN: {
    messages: {
      numeric:function(name){return name+""},
      required: function (name) { return name + ''},
      max:function(name,args){
        return name+''+args[0]+""
      },
      min:function(name,args){
        return name+''+args[0]+""
      },
      length:function(name,args){
        return name+''+args[0]+""
      },
      regex:function(name){return name+""},
      alpha_num:function(name,args){
        return name+"";
      },
      email:function(name){
        return name+"";
      }
    }
  }
})
Validator.extend('tel', tel)
Validator.extend('password', password)
Validator.extend("cnlength",cnLength)
Validator.extend("idnumber",idnumber);



