<template>
    <div>
        <div>
            <button type="button" class="btn btn-info" @click="inforAddClick"></button>
            <button type="button" class="btn btn-info" @click="updateData"></button>
            <button type="button" class="btn btn-info" @click="deleteData"></button>
            <button type="button" class="btn btn-info"></button>
            <button type="button" class="btn btn-info"></button>
        </div>
    </div>
</template>

<script>
export default {
  data: () => {
    return {};
  },
  props: ["checkData", "stateId"],
  methods: {
    inforAddClick() {
      this.$api.emitApi(this, { name: "add", title: "" }, true);
    },
    updateData() {
      var vm = this;
      var checkData = vm.checkData;
      var len = checkData.length;
      if (len > 1) {
        vm.$layer.alert("1");
      } else if (len <= 0) {
        vm.$layer.alert("");
      } else {
        vm.$api.emitApi(vm, { name: "update", title: "" }, true);
      }
    },
    deleteData() {
      var vm = this;
      var checkData = vm.checkData;
      var len = checkData.length;
      if (len > 1) {
        vm.$layer.alert("");
      } else if (len <= 0) {
        vm.$layer.alert('<div class="layer divtext"><i class="icon icon-tips"></i></div>');
      } else {
        var id=vm.$layer.confirm('<div class="layer divtext"><i class="icon icon-question"></i></div>', () => {
            vm.$layer.close(id);
          var datas = {
            id: checkData[0].id,
            states: {
              id: vm.stateId
            }
          };
          vm.$axios
            .post(vm.HOST+"deleteData.action", datas)
            .then(Response => {
              if (Response.code == "0") {
                vm.$layer.alert("");
              } else {
                vm.$layer.alert("");
              }
            })
            .catch(error => {
              vm.$layer.alert("");
            });
        });
      }
    }
  }
};
</script>

<style>
</style>



